@import '../../utils/breakpoints';

.account {
  display: grid;
  grid-template-columns: minmax(200px, 302px) minmax(400px, auto);
  grid-template-rows: auto-fit;
  column-gap: 16px;
  height: 80%;
  &__logout {
    display: none;
    font-size: 24px;
    line-height: 125.3%;
    color: #FF4238;
    margin-top: 65px;
    border: none !important;
  }
}

@media (max-width: $mobile-width) { 
  .account {
    animation: openPageScaling ease-in-out 0.4s forwards;
    grid-template-columns: auto;
    &__logout {
      display: block;
    }
  }
}